@Form.create()
export default class CPCompanyManage extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      tableLoading: false,
      selectedRowKeys: [],
      pageNo: 1,
      gameFormData:{}
    }
    // this.addData = this.addData.bind(this)
    // this.editData = this.editData.bind(this)
    // this.deletData = this.deletData.bind(this)
    this.onShowSizeChange = this.onShowSizeChange.bind(this)
    this.changePageNo = this.changePageNo.bind(this)
  }
  componentDidMount() {
    this.props.form.validateFields();
  }
  changePageNo(page, pagesize) {// 用来监听pageno变化
    console.log(page)
    console.log(pagesize)
  }
  onShowSizeChange(current, pageSize) { // 用来监听pagesize变化
    console.log(current, pageSize);
  }
  handleSubmit = (e) => {
    e.preventDefault();
    console.log(e)
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }
  render(){
    const {
      getFieldDecorator
    } = this.props.form;
    return(
      <div className="content fadein">
        <div>
          <div className="inputRight">
            <Form layout="inline" onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator('company', {})(
                  <Input placeholder="请输入合同名称" />
                )}
              </Form.Item>
              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                >
                  查询
                </Button>
              </Form.Item>
            </Form>
          </div>
            {/* <Button type="primary" style={{marginBottom:"20px"}} onClick={this.addData()} shape="round" icon="plus">添加</Button> */}
            <Table loading={this.state.tableLoading} pagination={false} className="nopadtable" columns={columns()} dataSource={data} />,
            <div className="mb20" style={{ textAlign: "right" }}>
              <Pagination showSizeChanger onChange={this.changePageNo} onShowSizeChange={this.onShowSizeChange} defaultCurrent={this.state.pageNo} total={32} />
            </div>
          </div>
      </div>
    )
  }
}




